#div-pokemons-nav {
	padding: 0 5px;
	margin-bottom: 10px;
}

#div-pokemons-nav > #div-pokemons-nav-links {
	margin-bottom: 10px;
	border-bottom: 1px solid white;
	padding: 5px;
}
#div-pokemons-nav > #div-pokemons-nav-links > * {
	display: inline-block;
	background-color: white;
	color: black;
	padding: 5px;
	margin: 0 5px 5px 0;
	border-radius: 5px;
	text-decoration: none;
	transition: background-color 0.5s;
}

#div-pokemons-nav > #div-pokemons-nav-links :hover {
	background-color: #ffde00;
}

#div-pokemons-nav > #div-pokemons-nav-links .link-active {
	background-color: #ffde00;
	font-weight: 500;
}

#div-pokemons-nav > div:nth-child(2) {
	display: flex;
	justify-content: flex-end;
}

#div-pokemons-nav > div:nth-child(2) > span {
	color: #b3a125;
	font-size: 1.5rem;
	line-height: 1.5rem;
}
#div-pokemons-nav > div:nth-child(2) > select {
	padding: 5px;
	border-radius: 5px;
	font-size: 1rem;
	border: 0;
	width: 150px;
}
