.div-pokemon-card {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	height: 340px;
	width: 19%;
	overflow: hidden;
	background-color: white;
	border-radius: 10px;
	box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.1);
	transition: transform 0.5s;
}
.div-pokemon-card > * {
	text-decoration: none;
	color: black;
}

.div-pokemon-card:hover {
	transform: translateY(-8px);
}

.div-pokemon-card .div-pokemon-image {
	height: 180px;
	display: flex;
	justify-content: center;
	align-items: center;
}

.div-pokemon-card img {
	max-height: 150px;
	max-width: 170px;
}

.div-pokemon-card .div-pokemon-info {
	background-color: #ffde00;
	padding: 20px;
	height: 160px;
}

.div-pokemon-card h2 {
	text-transform: capitalize;
}

.div-pokemon-card span.type {
	display: inline-block;
	text-transform: capitalize;
	font-size: 0.8rem;
	padding: 2px 5px;
	background-color: #b3a125;
	color: white;
	border-radius: 5px;
	margin: 5px 5px 0 0;
}

.div-pokemon-card small {
	display: inline-block;
	padding: 2px 5px;
	border-radius: 5px;
	color: white;
	background-color: #cc0000;
}

.div-pokemon-card .material-icons {
	font-size: 1rem;
	margin-right: 5px;
	color: #b3a125;
}
