.div-pokemon-modal-container {
	position: fixed;
	width: 100%;
	height: 100vh;
	top: 0;
	left: 0;
	overflow: hidden;
	z-index: 2;
	background-color: rgba(255, 255, 255, 0.5);
	display: flex;
	justify-content: center;
	align-items: center;
	overflow: hidden;
}

.div-pokemon-modal {
	height: 80%;
	width: 60%;
	background-color: white;
	border-radius: 10px;
	box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.1);
	display: grid;
	grid-template-columns: 55% 45%;
	padding: 40px 20px;
	overflow: hidden;
}

.div-pokemon-modal .div-pokemon-modal-left {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-between;
	overflow: hidden;
}

.div-pokemon-modal-left .div-pokemon-info {
	width: 80%;
	background-color: #ffde00;
	padding: 10px;
	text-align: center;
	border-radius: 10px;
	overflow: hidden;
}

.div-pokemon-modal-left .div-pokemon-info h2 {
	text-transform: capitalize;
}

.div-pokemon-modal-left .div-pokemon-info span.type {
	display: inline-block;
	text-transform: capitalize;
	font-size: 0.8rem;
	padding: 2px 5px;
	background-color: #b3a125;
	color: white;
	border-radius: 5px;
	margin: 5px 5px 0 0;
}

.div-pokemon-modal-left .div-pokemon-info small {
	display: inline-block;
	padding: 2px 5px;
	border-radius: 5px;
	color: white;
	background-color: #cc0000;
	margin-right: 5px;
}

.div-pokemon-modal-left .div-pokemon-info .material-icons {
	font-size: 1rem;
	margin-right: 5px;
	color: #b3a125;
}

.div-pokemon-modal-left > img {
	max-height: 200px;
	max-width: 200px;
}

.div-pokemon-modal-left .div-pokemon-evolution {
	display: flex;
	flex-direction: row;
	justify-content: center;
}

.div-pokemon-modal-left .div-pokemon-evolution > * {
	text-decoration: none;
}

.div-pokemon-modal-left .div-pokemon-evolution > div {
	max-width: 25%;
	overflow: hidden;
}

.div-pokemon-modal-left .div-pokemon-evolution > p {
	min-width: 12.5%;
	display: flex;
	flex-direction: row;
	align-self: center;
	justify-content: center;
	text-decoration: none;
}

.div-pokemon-modal-left .div-pokemon-evolution .div-image-container {
	width: 100%;
	height: 100px;
	display: flex;
	align-items: center;
	justify-content: center;
}

.div-pokemon-modal-left .div-pokemon-evolution div img {
	max-width: 100%;
	max-height: 100px;
}

.div-pokemon-modal-left .div-pokemon-evolution div > p:nth-of-type(1) {
	background-color: #eee;
	color: black;
	padding: 2px;
	text-align: center;
	border-radius: 5px;
}

.div-pokemon-modal-left .div-pokemon-evolution .p-pokemon-evolution-info {
	display: inline-flex;
	flex-flow: row wrap;
}

.div-pokemon-modal-left .div-pokemon-evolution .small-pokemon-id {
	padding: 0 2px;
	border-radius: 5px;
	color: white;
	background-color: #cc0000;
	font-size: 0.7rem;
}

.div-pokemon-modal-left .div-pokemon-evolution .small-pokemon-type {
	text-transform: capitalize;
	padding: 0 2px;
	background-color: #b3a125;
	color: white;
	border-radius: 5px;
	margin-left: 2px;
	font-size: 0.7rem;
}

.div-pokemon-modal-right {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	padding: 10px 20px 0 20px;
}

.div-pokemon-modal-right .div-pokemon-characteristics p {
	margin-bottom: 5px;
}

.div-pokemon-modal-right .div-pokemon-characteristics p span {
	background-color: #b3a125;
	color: white;
	padding: 2px 5px;
	margin-left: 5px;
	border-radius: 5px;
	font-size: 0.8rem;
}

.div-pokemon-modal-right .div-pokemon-stats {
	background-color: #eee;
	padding: 10px 20px;
	border-radius: 10px;
}

.div-pokemon-modal-right .div-pokemon-stats > p {
	font-size: 0.8rem;
	margin-bottom: 10px;
}

.div-pokemon-modal-right .div-pokemon-stats .bar-container {
	width: 100%;
	height: 12px;
	background-color: white;
	overflow: hidden;
	position: relative;
}

.div-pokemon-modal-right .div-pokemon-stats .bar-container .bar-fill {
	height: 12px;
	background-color: #3b4cca;
}

.div-pokemon-modal-right .div-pokemon-stats .bar-container p {
	position: absolute;
	width: 100%;
	height: 12px;
	line-height: 10px;
	font-size: 12px;
	padding: 1px 0;
	color: lavender;
	text-align: center;
}

.div-pokemon-modal-right .btn-back {
	display: inline-block;
	text-decoration: none;
	background-color: #cc0000;
	border-radius: 5px;
	padding: 2px 5px;
	color: white;
	width: 50px;
	margin-right: 10px;
	text-align: center;
	align-self: flex-end;
}
